@import url('https://fonts.googleapis.com/css?family=Open+Sans');

@font-face {
    font-family: "Berlin";
    src: url("BRLNSR.TTF");
}

.panneau {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
}

.page {
    font-family: 'Berlin', sans-serif;
    text-align: center;
    background-color: pink;
    margin: 1px;
}

.number {
    display: none;
}

@media not print {
    .page {
        font-size: 16px;
    }
}

@media print {
    .break {
        page-break-after: always;
    }

    .panneau {
        display: initial;
    }

    .portrait.page {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        margin: 0px;
    }

    .landscape.page {
        text-align: left;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        margin: 0px;
    }

    .portrait .letter {
        -ms-flex-positive: 1;
            flex-grow: 1;
    }

    .landscape .spacer {
        -ms-flex-positive: 1;
            flex-grow: 1;
    }

    .number {
        -ms-flex-item-align: end;
            align-self: flex-end;
        display: initial;
        font-size: 15px;
        min-height: 20px;
    }

    .portrait {
        width: 190mm;
        height: 276mm;
    }

    .landscape {
        width: 276mm;
        height: 188mm;
    }

    .half-landscape {
        width: 190mm;
        height: 139mm;
    }

    .no-print {
        display: none;
    }

    body {
        background: none;
        -ms-zoom: 1.665;
    }

    .portrait, .landscape,  .half-landscape{
        margin: 0;
        padding: 0;
        border: none;
    }

    .landscape {
        margin-bottom: 10px;
        -webkit-transform-origin: 0 0;
            -ms-transform-origin: 0 0;
                transform-origin: 0 0;
    }
}



